.main{
    padding: 20px;
}

.logo{
    color: white;
    font-weight: bold;
}

.role{
    color: white;
    font-size: 14px;
    margin-top: 20px;
}

.sidebar{
    background-color: #005555;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    margin-right: 20px;
    min-height: 100%;
    padding: 10px;
    white-space: nowrap;
}

.content{
    width: 100%;
    height: 100%;
}

.header{
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    padding: 10px;
    margin-bottom: 20px;
    height: 10vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.body {
    background-color: white;
    border-radius: 5px;
    box-shadow: 0 0 2px gray;
    height: 82vh;   
    width: 100%;
    overflow-y: auto;    
    overflow-x: scroll !important;  
    padding: 15px;
}

.menu{
    margin-top: 100px;
    padding: 0 10px;
}

.menu-item{
    margin-top: 30px;
    cursor: pointer;
    color: white;
}

.menu-item a{
    color: rgba(255, 255, 255, 0.726);
    text-decoration: none;
    font-size: 18px;
}

.menu-item i{
    color: rgba(255, 255, 255, 0.726);
    text-decoration: none;
    font-size: 18px;
    margin: 0 15px;
}

.active-menu-item{
    color: white;
    background-color: #013737;
    padding: 5px;
    border-radius: 5px;
}

.header-action-icons{
    font-size: 30px;
    color: black !important;
    cursor: pointer;
    padding-left: 10px;
}


.ant-scroll-number-only-unit{
    padding-right: -10px !important;
    padding-bottom: -10px !important;
}

.underline {
    text-decoration: underline;
}



  

@media screen and (max-width: 768px){
    .menu{
        padding: 0 5px;
    }
    .doctorimg{
        height: 30px;
    }
}